// pages/viewBindOwner/viewBindOwner.js
const context = require("../../context/Java110Context.js");
const constant = context.constant;

export default {
	data() {
		return {
			steps: [{
				cuIcon: 'usefullfill',
				name: '申请',
				desc: ''
			}, {
				cuIcon: 'radioboxfill',
				name: '审核中',
				desc: ''
			}, {
				cuIcon: 'roundcheckfill',
				name: '完成',
				desc: ''
			}],
			active: 0,
			areaName: '',
			communityId: '',
			communityName: '',
			appUserName: '',
			appUserId: '',
			idCard: '',
			link: ''
		};
	},

	components: {},
	props: {},

	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		context.onLoad(options);
		this.loadOwnerInfo();
	},

	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady: function() {},

	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {},

	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {},

	/**
	 * 生命周期函数--监听页面卸载
	 */
	onUnload: function() {},

	/**
	 * 页面相关事件处理函数--监听用户下拉动作
	 */
	onPullDownRefresh: function() {},

	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {},

	/**
	 * 用户点击右上角分享
	 */
	onShareAppMessage: function() {},
	methods: {
		/**
		 * 加载业主信息
		 */
		loadOwnerInfo: function() {
			let _that = this;

			context.getOwner(function(_ownerInfo) {
				if (_ownerInfo) {
					let _active = _ownerInfo.state == '10000' ? 1 : 2;

					_that.areaName = _ownerInfo.parentAreaName + _ownerInfo.areaName;
					_that.communityId = _ownerInfo.communityId;
					_that.communityName = _ownerInfo.communityName;
					_that.appUserName = _ownerInfo.appUserName;
					_that.appUserId = _ownerInfo.appUserId;
					_that.idCard = _ownerInfo.idCard;
					_that.link = _ownerInfo.link;
					_that.active = _active;

				}
			});
		},
		unbindOwner: function() {
			//调用解绑业主，并且删除本地缓存
			let obj = {
				"appUserId": this.appUserId,
				"communityId": this.communityId
			};
			let msg = "";

			if (obj.appUserId == "" || obj.communityId == "") {
				msg = "数据异常";
				wx.showToast({
					title: msg,
					icon: 'none',
					duration: 2000
				});
				return;
			}

			context.request({
				url: constant.url.appUserUnBindingOwner,
				header: context.getHeaders(),
				method: "POST",
				data: obj,
				//动态数据
				success: function(res) {
					console.log(res);

					if (res.statusCode != 200) {
						wx.showToast({
							title: '解绑失败',
							icon: 'none',
							duration: 2000
						});
						return;
					} //成功情况下跳转


					wx.redirectTo({
						url: "/pages/bindOwner/bindOwner"
					});
				},
				fail: function(e) {
					wx.showToast({
						title: "服务器异常了",
						icon: 'none',
						duration: 2000
					});
				}
			});
		}
	}
};
